.company {
	position: relative;
	height: 100%;
	&__bg {
		position: relative;
		width: 100%;
		height: 100%;
		@media screen and (max-width: 68.75em) {
			padding-bottom: 70%; } }
	&__link {
		.link__logo {
			opacity: 1; } }
	&__link:hover .link__layer {
		background-color: black;
		opacity: 0.3; } }



.product {
	@include column;
	&__descr {
		flex: 2;
		display: block; }

	&__photo {
		flex: 1;
		padding-bottom: 37%; } }

