@import 'fonts';
@import 'colors';

@mixin centered {
	display: flex;
	align-items: center;
	justify-content: center; }

@mixin center {
	align-items: center;
	justify-content: center; }

@mixin absolute {
	position: absolute;
	top: 0;
	left: 0; }

@mixin layer {
	@include absolute;
	width: 100%;
	height: 100%; }

@mixin column {
	display: flex;
	flex-direction: column; }

.rr {
	flex-direction: row-reverse; }

html {
	font-size: 62.5%; }
body {
	@include f-lato; }

.container {
	max-width: 111rem;
	display: flex;
	width: 100%; }

.ghost-link {
	display: block;
	cursor: pointer;
	position: absolute;
	width: 100%;
	height: 100%;
	z-index: 100; }

.link {
	@include layer;
	@include centered;
	@include column;
	&__layer {
		@include layer;
		z-index: 1;
		transition: 0.4s;
		opacity: 0.7; }
	&__logo {
		z-index: 2; }
	&__text {
		color: #ffffff;
		font-family: Lato;
		font-size: 14px;
		font-weight: 700;
		text-transform: uppercase;
		letter-spacing: 2.8px;
		z-index: 2;
		margin-top: 1.4rem; }
	&__logo, &__text {
		opacity: 0;
		transition: 0.5s; }
	&:hover {
		.link {
			&__layer {
				background-color: $green; }
			&__logo, &__text {
				opacity: 1; } } } }

// GRID
.row {
	display: flex;
	position: relative;
	@media screen and (max-width: 68.75em) {
		flex-direction: column; } }

.row-sm {
	display: flex; }


.main-col {
	flex: 2; }
.col, .side-col {
	flex: 1;
	position: relative;
	@include column; }


.fh {
	height: 100%; }

.space-between {
	justify-content: space-between; }

.btn {
	font-family: Lato;
	text-transform: uppercase;
	display: block;
	color: white;
	border: none; }


@import 'sections/header';
@import 'sections/products';
@import 'sections/testimonials';
@import 'sections/galery';
@import 'sections/mailing';
@import 'sections/footer';

@import 'blocks/card';
